import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import axios from 'axios';
import Button from '../components/Button/Button';
import useGeoLocation from '../hooks/useGeoLocation/useGeoLocation';
import styles from './Home.module.scss'
import Icon from '../components/Icon/Icon';

const LINK = 'https://app-ser-weath.herokuapp.com/';
// const LINK = 'http://localhost:8080/';

const Home = () => {
    const [dis, setDis] = useState(false);
    const [res, setRes] = useState([]);
    const [weth, setWeth] = useState(null);
    const [takeInput, setTakeInput] = useState({
        city: '',
    });
    const [countTemp, setCountTemp] = useState(weth !== null ? weth.consolidated_weather[0].the_temp : null );
    const [query, setQuery] = useState(null);

    let styleBg= '';

    let location = useGeoLocation();
    const lat = location.coordinates.lat;
    const lng = location.coordinates.lng;
    const lattlong = {lattlong: `${lat},${lng}`};

    const clic = () => {
    setDis(!dis);
    
    axios({
        method: 'post',
        url: `${LINK}lattlong`,
        data: lattlong
        
       }).then(resp=>  setRes(resp.data[0])  )
    }
    
    const getWether = () => {
         const woeid = {woed: `${res.woeid}`};
    
        axios({
             method: 'post',
             url: `${LINK}woeid`,
             data: woeid
        })
        .then((resp)=> setWeth(resp.data))
  
    }

    const enterInput = () => {
         const quer = {query:`${takeInput.city}`}
         console.log("quer", quer)
         setDis(true);
         axios({
             method: 'post',
            url: `${LINK}query`,
            data: quer
        })
        .then((resp)=> setRes(resp.data[0]))
    }

    const handleChange = e => {
        setTakeInput({ ...takeInput, [e.target.name]: e.target.value });
    };

    useEffect(()=>{
        if(res.woeid !== undefined) {
            getWether();
        }
    },[res.woeid,styleBg]);

    if(res !== undefined){
        console.log(res)
    }
    
    const canc = () => {}

    if(weth !== null){
     if (countTemp < -10) {
           styleBg = styles.bg_cold;
     } 
      else if(countTemp >= +30) {
          styleBg = styles.bg_hot;
      }
     else{
           styleBg = styles.bg_norm;
     }
    }
    
    return (
        <section className={styleBg}>
            <div className={styles.center}>
            <h4>"Accept your GPS data"</h4>
            <Icon type='h'/>
            <div>
              <Button type='button' title='Ok' onClick={clic} disabled={dis} /> 
              <Button type='button' title='Cancel' onClick={canc}/>  
            </div>
            <p>Your location : {location.loaded ? JSON.stringify(location) 
            : "Location data not available yet"}</p>
            <form  noValidate>
                <input type='text' name='city' placeholder='Enter city name' value={takeInput.city} onChange={handleChange}/>
                <Button type='button' title='Enter' onClick={enterInput}/>
            </form>
            {dis && <div className={styles.box}>
                
            <p>
                Your city: {location.loaded ? res.title 
            : "Location data not available yet"}
            </p>
            {/* {weth.consolidated_weather[0].weather_state_abbr} */}
            <p >
                Your weather in the town: {weth !== null ? <p>
                    <Icon type='h'/>
                    <span className={styles.tod}>{weth.consolidated_weather[0].the_temp}&deg;C</span>
                    <span>, temperature from {weth.consolidated_weather[0].min_temp}&deg;C</span>
                    <span> to {weth.consolidated_weather[0].max_temp}&deg;C,</span>
                    <span> clouds: {weth.consolidated_weather[0].weather_state_name}</span>
                    
                    <br/>
                    <input type='text' value={countTemp}/>
                    <input type='range' min={weth.consolidated_weather[0].min_temp} max={weth.consolidated_weather[0].max_temp} step='any' value={countTemp} onChange={(event) => setCountTemp(event.target.value)}/>
                    </p>
            : "Weather data not available yet"}
            
            </p>
            
            </div>}
            </div>       
        </section>
    )
}

export default Home;
