import React from 'react'
import PropTypes from 'prop-types';
import * as icons from '../../theme/weather_icon';

function Icon (props){
    const {type, color} = props;

    const iconJsx = icons[type];

    if(!iconJsx) {
        return null;
    }
    console.log("🚀 ~ file: Icon.js ~ line 20 ~ Icon ~ iconJsx", iconJsx)
 
    return (
        <span>
          {iconJsx}  
        </span>
    )
};

export default Icon;
